import React, {Component} from 'react';
import {Route, Link} from 'react-router-dom';
import {Grid, Row, Col, Button, Collapse, FormControl, FormGroup, ControlLabel} from 'react-bootstrap';
import * as usersAPI from './usersAPI';

export class AddUser extends React.Component {
    constructor(props) {
      super(props);
      this.state = {
        name: '',
        username: '',
        email: '',
      }
      this.handleNameChange = this.handleNameChange.bind(this);
      this.handleUsernameChange = this.handleUsernameChange.bind(this);
      this.handleEmailChange = this.handleEmailChange.bind(this);
    }

    handleNameChange(event) {
        this.setState({name: event.target.value})
    }
    handleUsernameChange(event) {
        this.setState({username: event.target.value})
    }
    handleEmailChange(event) {
        this.setState({email: event.target.value})
    }

    render() {
      return(
        <Grid>
          <Row>
            <form onSubmit={() => this.props.userCreate(this.props.users.length+1, this.state.name, this.state.username, this.state.email)}>
              <FormGroup>
                <Col md={4}>
                  <ControlLabel className="add-user-name">
                    Name:
                    <FormControl type="text" value={this.state.name} onChange={this.handleNameChange}/>
                  </ControlLabel>
                </Col>
                <Col md={4}>
                  <ControlLabel className="add-user-username">
                    Username:
                    <FormControl type="text" value={this.state.username} onChange={this.handleUsernameChange}/>
                  </ControlLabel>
                </Col>
                <Col md={4}>
                  <ControlLabel className="add-user-email">
                    E-mail:
                    <FormControl type="text" value={this.state.email} onChange={this.handleEmailChange}/>
                  </ControlLabel>
                </Col>
                <Col md={12}>
                  <Button bsStyle="primary" type="submit" value="submit">Submit</Button>
                </Col>
              </FormGroup>
            </form>
          </Row>
        </Grid>
      )
    }

}



