import React, {Component} from 'react';
import {Route} from 'react-router-dom';
import {Link} from 'react-router-dom';
import {Grid, Row, Col, Button, Collapse, Table} from 'react-bootstrap';
import * as usersAPI from './usersAPI';
import logo from './logo.svg';
import './App.css';
import { UserList } from './UserList';
import { AddUser } from './AddUser';

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {
      users: []
    }
    this.userDelete = this.userDelete.bind(this);
    this.userCreate = this.userCreate.bind(this);
    this.userEdit = this.userEdit.bind(this);
  }

  componentDidMount() {
    usersAPI.read().then(users => {
      this.setState({users: users})
    })
  }

userDelete(user) {
  console.log(user);
  usersAPI.userDelete(user.id)
  .then(response => response.json())
  .then(json => console.log(json))
  usersAPI.read().then(users => {
    this.setState({users: users})
  })
}

userCreate(id, name, username, email) {
  console.log(id, name, username, email);
  usersAPI.userCreate(id, name, username, email)
  .then(response => response.json())
  .then(json => console.log(json))
  usersAPI.read().then(users => {
    this.setState({users: users})
  })
}

userEdit(id, name, username, email) {
  console.log(id, name, username, email);
  usersAPI.userEdit(id, name, username, email)
  .then(response => response.json())
  .then(json => console.log(json))
  usersAPI.read().then(users => {
    this.setState({users: users})
  })
}

  render() {
    return (<div className="App">
      <header className="App-header">
        <img src={logo} className="App-logo" alt="logo"/>
        <h1 className="App-title">My Simple CRUD App</h1>
      </header>
      <Grid className="main-app">
        <Row>
          <Col ms={12}>
          <Route path ='/add' render={() => (
                    <div className='display'>
                      <Link to='/' ><Button bsStyle="primary">Cancel</Button></Link>
                      <AddUser userCreate={this.userCreate} users={this.state.users}/>
                    </div>
                )} />
          <Route exact path='/' render={() => (
          <div className='display'>
            <Link to='/add'><Button bsStyle="primary">Add user</Button></Link>
            </div>
          )} />
          <Route path='/' render={() => (
            <div className="display">
              <Table striped bordered condensed hover responsive>
                <thead>
                  <tr>
                    <th>ID</th>
                    <th>Name</th>
                    <th>Username</th>
                    <th>Email</th>
                    <th></th>
                  </tr>
                </thead>
                {this.state.users.map(user =><UserList user={user} userDelete={this.userDelete} userEdit={this.userEdit} />)}
            </Table>
            </div>
          )} />
          </Col>
        </Row>
      </Grid>
    </div>);
  }
}

export default App;



