import React from 'react';
import {Link, Route} from 'react-router-dom';
import {Grid, Row, Col, Button, Collapse, Table} from 'react-bootstrap'
import {EditUser} from './EditUser'

export class UserList extends React.Component {
  render() {
    return(
      <tr className="user">
        <td className='user-id'>{this.props.user.id}</td>
        <td className='user-first-name'>{this.props.user.name}</td>
        <td className='user-username'>{this.props.user.username}</td>
        <td className='user-email'>{this.props.user.email}</td>
        <td>
              <div className='btn-cont'>
              <Route exact path={'/'} render={()=> (
                <Link to={`/${this.props.user.id}`} >
                  <Button bsStyle="primary">Edit this user</Button>
                </Link>
              )}/>
                <Route exact path={`/${this.props.user.id}`} render={() => (
                  <Link to='/'>
                    <Button bsStyle="primary">Cancel edition</Button>
                  </Link>)} />
              </div>
              <div className='btn-cont'>
              <Route path={`/${this.props.user.id}`} render={() => (<EditUser user={this.props.user} userEdit={this.props.userEdit} />)} />
              </div>
              <div className='btn-cont'>
              <Button onClick={() => this.props.userDelete(this.props.user)}>Delete this user</Button>
              </div>
        </td>
      </tr>
    )
  }
}



