const api = "http://jsonplaceholder.typicode.com/users";
const headers = {"Content-type": 'application/json; charset=UTF-8'};

export const read = () =>
fetch(api)
.then(response => response.json());


export const userDelete = (id) =>
fetch(`${api}/${id}`, {
  method: 'DELETE'
})

export const userCreate = (id, name, username, email) =>
fetch(api, {
  method: 'POST',
  body: JSON.stringify({
    id: id,
    name: name,
    username: username,
    email: email
  }),
  headers: headers
})

export const userEdit = (id, name, username, email) =>
fetch(api,{
  method: 'PUT',
  body: JSON.stringify({
    id: id,
    name: name,
    username: username,
    email: email
  }),
  headers: headers
})



